import  next from "../icons/next.png";
import  prev from "../icons/prev.png";


$(document).ready(function(){
    $('.slider').slick({
        infinite: true,
        speed: 300,
        slidesToShow: 1,
        //adaptiveHeight: true,
        prevArrow: `<button type="button" class="slider__prev"><img class="slider__arrow" src=${ prev }></button>`,
        nextArrow: `<button type="button" class="slider__next"><img class="slider__arrow" src=${ next }></button>`,
        responsive: [
            {
              breakpoint: 991,
              settings: {
                infinite: true,
              }
            },
            {
              breakpoint: 767,
              settings: {
                arrows:false,
              }
            },
         
        ]
    });
    $('ul.catalog__tabs').on('click', 'li:not(.active)', function() {
      $(this)
        .addClass('catalog__tab_active').siblings().removeClass('catalog__tab_active')
        .closest('div.catalog__container').find('div.catalog__content').removeClass('catalog__content_active').eq($(this).index()).addClass('catalog__content_active');
    });
    
    function toggleSlide(link) {
        $(link).each(function(i) {
          $(this).on("click", function(e) {
              e.preventDefault();
              $(".card__content").eq(i).toggleClass("card__content_active");
              $(".card__list").eq(i).toggleClass("card__list_active");
          });
       });
    }
    toggleSlide(".card__link");
    toggleSlide(".card__back");
    //Modal

    $("[data-modal=consultation]").on("click", function(){
        $('.overlay, #consultation').fadeIn("slow");
    });
    $(".modal__close").on("click", function(){
      $('.overlay, #consultation, #order, #thanks').fadeOut("slow");
      
    });
    
    $(".button_size_mini").each(function(i){
      $(this).on("click", function(){
          $("#order .modal__desc").text($(".card__title").eq(i).text());
          $('.overlay, #order').fadeIn("slow");
      });
    });
    
    function validateForms(form) {
        $(form).validate({
            rules: {
                name: {
                  required:true,
                  minlength:2,
                },
                phone: "required",
                email: {
                  required:true,
                  email:true,
                },
            },
            messages:{
              name: {
                required:" ",
                minlength:jQuery.validator.format("   {0} ")
              },
              phone:" ",
              email: {
                required:" email",
                email:"  "
              }
          
            }
      });
    }
    validateForms("#consultation-form");
    validateForms("#consultation form");
    validateForms("#order form");

    $("input[name=phone]").mask("+7(999) 999-99-99");

    $("form").submit(function(e){
        e.preventDefault();
    });

    //Smooth scroll 

    $(window).scroll(function(){
        if ($(this).scrollTop() > 1600) {
            $(".pageup").fadeIn();
        } else {
          $(".pageup").fadeOut();
        }
    });
    $(".pageup").click(function() {
      let href = $(this).attr("href");
      $("html,body").animate({
        scrollTop:$(href).offset().top + "px"
      });
      return false;
    });
     // animation library
      new WOW().init(); ;
});